
.agile__dots {
  bottom: 0;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
}
.agile__dot button {
  background-color: transparent;
  border: 1px solid #fff;
}
.agile__dot button:hover {
    background-color: #fff;
}
.agile__dot--current button {
  background-color: #fff;
}
.agile__arrow {
  height: 100%;
  top: 0;
  width: 80px;
}
.agile__arrow:hover {
    background-color: rgba(0, 0, 0, 0.5);
}
.agile__arrow:hover #arrow-svg {
      fill: #fff;
}
.agile__arrow[disabled] {
    display: none;
}
.agile__arrow #arrow-svg {
    fill: rgba(255, 255, 255, 0.4);
    height: 25px;
}
.slide {
  background-position: center;
  background-size: cover;
  height: 300px;
}
.slide--1 {
    background-image: url(../../../static/img/car1.jpg);
}
.slide--2 {
    background-image: url(../../../static/img/car2.jpg);
}
.slide--3 {
    background-image: url(../../../static/img/car3.jpg);
}
.slide--4 {
    background-image: url(../../../static/img/car4.jpg);
}



