
.not-found-page .full-page > .content,
.not-found-page .centered {
  min-height: calc(100vh - 160px);
}
.not-found-page .not-found-icon {
  margin-right: 10px;
}
.not-found-page .centered {
  display: flex;
  padding-bottom: 150px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.not-found-page .centered h1, .not-found-page .centered i {
    font-size: 50px;
}
.not-found-page .centered p {
    font-size: 20px;
}



