
.vue-form-wizard .wizard-icon-circle.tab_shape {
  background-color: #9A9A9A !important;
  color: white;
}
.vue-form-wizard .wizard-tab-content {
  display: flex;
}
.vue-form-wizard .wizard-tab-content .wizard-tab-container {
    display: block;
    animation: fadeIn 0.5s;
}
.not-found-page .full-page > .content,
.not-found-page .centered {
  min-height: calc(100vh - 160px);
}
.not-found-page .not-found-icon {
  margin-right: 10px;
}
.not-found-page .centered {
  display: flex;
  padding-bottom: 150px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.not-found-page .centered h1, .not-found-page .centered i {
    font-size: 50px;
}
.not-found-page .centered p {
    font-size: 20px;
}



